@use "../abstracts/" as *;

header {
  .navbar {
    position: absolute;
    z-index: 1;
    width: 1322px;
    height: 91px;
    left: calc(50% - 1322px / 2);
    top: 0px;
    .navbar-brand {
      position: absolute;
      width: 187px;
      height: 58px;
      left: calc(50% - 187px / 2 - 431.5px);
      top: calc(50% - 58px / 2 + 0.5px);
      .brand_name {
        /* Brandname */

        position: absolute;
        width: 141px;
        height: 32px;
        left: 0px;
        top: 13px;

        /* h3 */

        font-family: "Montserrat";
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        /* identical to box height, or 133% */

        letter-spacing: 0.1px;

        /* light-gray-2 */

        color: #ececec;
      }
    }

    .navbar-collapse {
      /* collapse navbar-collapse */
      position: absolute;
      width: 815px;
      height: 58px;
      left: calc(50% - 815px / 2 + 110.5px);
      top: 16px;
      .nav-list {
        /* navbar-nav */
        /* Auto layout */
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 0px;
        gap: 21px;
        position: absolute;
        width: 275px;
        height: 24px;
        left: 0px;
        top: calc(50% - 24px / 2);
        .nav-list__item a {
          /* link */

          font-family: "Montserrat";
          font-style: normal;
          font-weight: 700;
          font-size: 14px;
          line-height: 24px;
          /* identical to box height, or 171% */

          text-align: center;
          letter-spacing: 0.2px;

          /* light-text-color */

          color: #ffffff;
        }
      }

      .navbar-nav {
        /* navbar-nav */
        /* Auto layout */
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 0px;
        gap: 45px;
        position: absolute;
        width: 300px;
        height: 52px;
        right: 0px;
        top: calc(50% - 52px / 2);
        .title {
          width: 41px;
          height: 22px;
          left: calc(50% - 41px / 2);
          top: calc(50% - 22px / 2);

          /* btn-text */

          font-family: "Montserrat";
          font-style: normal;
          font-weight: 700;
          font-size: 14px;
          line-height: 22px;
          /* identical to box height, or 157% */

          text-align: right;
          letter-spacing: 0.2px;

          /* light-text-color */

          color: #ffffff;
        }

        .button {
          /* button.btn.primary-color */

          /* Auto layout */

          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 15px 25px;
          gap: 15px;

          width: 137px;
          height: 52px;
          left: 0px;
          top: 0px;

          /* primary-color */

          background: #ff6551;
          color: white;
          border-radius: 5px;
        }
      }
    }

    .narbar-toggle {
      /* navbar-toggler */

      position: absolute;
      width: 24px;
      height: 13.71px;
      left: 1298px;
      top: 39px;
    }
  }
}
