@use "../abstracts/" as *;

header {
  background: #ffffff;
  flex: none;
  order: 0;
  flex-grow: 0;
  .background {
    /* background */
    position: absolute;
    height: 100vh;
    z-index: 1;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 205px;
    .banner-sec {
      @include bg-img("../assets/images/background/Banner.jpg");
      // background: rgba(0, 0, 0, 0.5);
      height: 100vh;
      position: absolute;
      z-index: 2;
      left: 0px;
      right: 0px;
      bottom: 0px;
    }
    .filter-sec {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: -19px;
      z-index: 3;
      background: rgba(0, 0, 0, 0.5);
    }
  }

  .introduce-sec__content {
    z-index: 1;
    /* container */
    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0px;
    gap: 80px;

    position: absolute;
    width: 1046px;
    height: 1006px;
    left: calc(50% - 1046px / 2);
    top: 104px;
    .main-content {
      /* main-content */

      /* Auto layout */

      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 40px 0px;
      gap: 40px;

      width: 699px;
      height: 466px;

      .header-tag-sec {
        width: 59px;
        height: 24px;

        /* h5 */

        font-family: "Montserrat";
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        /* identical to box height, or 150% */

        letter-spacing: 0.1px;

        /* secondary-text-color-2 */

        color: #ffc652;
      }
      .headline-sec {
        width: 542px;
        height: 160px;

        /* h1 */

        font-family: "Montserrat";
        font-style: normal;
        font-weight: 700;
        font-size: 58px;
        line-height: 80px;
        /* or 138% */

        text-align: center;
        letter-spacing: 0.2px;

        /* light-text-color */

        color: #ffffff;
      }
      .sub-headline {
        /* h4 #sub-headline sec- */

        width: 596px;
        height: 30px;

        /* h4 */

        font-family: "Montserrat";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        /* identical to box height, or 150% */

        text-align: center;
        letter-spacing: 0.2px;

        /* light-text-color */

        color: #ffffff;
      }
      .button {
        /* button Md sec- */

        /* Auto layout */

        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 15px 40px;
        gap: 10px;

        width: 133px;
        height: 52px;

        /* primary-color */

        background: #ff6551;
        border-radius: 5px;
        color: white;
      }
    }
    .listCard-content {
      /* Auto layout */

      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 0px;
      gap: 30px;

      width: 1046px;
      height: 300px;
    }
  }
}
