.introduce-sec-card {
  /* card */

  /* Auto layout */

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 35px 40px;
  gap: 20px;

  width: 328px;
  height: 300px;

  /* light-background-color */

  background: #ffffff;
  /* accentued-drop-shadow */

  box-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);

  .fixed-width {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 22px 19px;
    gap: 10px;
    width: 70px;
    height: 76px;
    background: #b6d7ff;
    border-radius: 10px;
    .cool-icon {
      width: 32px;
      height: 32px;
    }
  }

  .feature-title-sec {
    width: 205px;
    height: 32px;

    /* h3 */

    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    /* identical to box height, or 133% */

    letter-spacing: 0.1px;

    /* text-color */

    color: #252b42;
  }

  .line-div {
    width: 50px;
    height: 2px;

    /* danger-color */

    background: #e74040;
  }

  .feature-description {
    width: 222px;
    height: 60px;

    /* paragraph */

    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    /* or 143% */

    letter-spacing: 0.2px;

    /* second-text-color */

    color: #737373;
  }
}
